* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  background-color: #212121;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

li {
  list-style: none;
}

.wrapper {
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 100vh;
  display: flex;
}

a {
  color: #fff;
  text-decoration: none;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 5px;
  outline: none;
  padding: 10px 20px;
}

.backbtn {
  border: none;
}

.timer {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

input, .flatpickr-input {
  color: #fff;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 5px;
  outline: none;
  padding: 10px 20px;
}

.flatpickr-input svg {
  color: #fff;
}

.fields {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.promise-label {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.promise-button {
  margin-top: 30px;
}

.column {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

